
.message-renderer-demo {
  padding: 20px;
  max-width: 1400px;
  margin: 0 auto;
  
  h1 {
    color: #333;
    border-bottom: 2px solid #0066cc;
    padding-bottom: 10px;
    margin-bottom: 20px;
  }
  
  .demo-container {
    display: flex;
    gap: 20px;
    
    .control-panel {
      flex: 0 0 400px;
      background: #f5f5f5;
      padding: 20px;
      border-radius: 8px;
      
      h3 {
        margin-top: 0;
        color: #333;
      }
      
      .control-group {
        margin-bottom: 15px;
        
        label {
          display: block;
          margin-bottom: 5px;
          color: #666;
        }
        
        select, textarea {
          width: 100%;
          padding: 8px;
          border: 1px solid #ddd;
          border-radius: 4px;
        }
        
        button {
          padding: 8px 16px;
          background: #0066cc;
          color: white;
          border: none;
          border-radius: 4px;
          cursor: pointer;
          margin-right: 10px;
          
          &:hover {
            background: #0052a3;
          }
          
          &:disabled {
            background: #ccc;
            cursor: not-allowed;
          }
        }
        
        input[type="checkbox"] {
          margin-right: 5px;
        }
      }
    }
    
    .render-panel {
      flex: 1;
      
      h3 {
        margin-top: 0;
        color: #333;
      }
      
      .message-wrapper {
        background: white;
        border: 1px solid #ddd;
        border-radius: 8px;
        padding: 20px;
        min-height: 300px;
        max-height: 600px;
        overflow-y: auto;
      }
      
      .performance-stats {
        margin-top: 20px;
        background: #f9f9f9;
        border: 1px solid #e1e4e8;
        border-radius: 4px;
        padding: 15px;
        
        h4 {
          margin-top: 0;
          color: #666;
        }
        
        pre {
          background: white;
          padding: 10px;
          border-radius: 4px;
          overflow-x: auto;
          font-size: 12px;
        }
      }
    }
  }
}

// 响应式布局
@media (max-width: 1024px) {
  .message-renderer-demo {
    .demo-container {
      flex-direction: column;
      
      .control-panel {
        flex: none;
        width: 100%;
      }
    }
  }
}
